import * as echarts from 'echarts';
import { useEffect } from 'react';

function MyChart(props){
  useEffect(() => {
    var myChart = echarts.init(document.getElementById('main'));
    // Draw the chart
    myChart.setOption({
      title: {
        text: ''
      },
      tooltip: {},
      xAxis: {
        type: 'category',
        data: ['没有遭遇', '遭遇一种', '遭遇二种', 
        '遭遇三种', '遭遇四种'],
        axisLabel: {
            interval:0,//代表显示所有x轴标签显示
            rotate:30
        }
      },
      yAxis: {
        type: 'value'
      },
      series: [
        {
          type: 'bar',
          data: [40.1, 48.1, 55.1, 60.6, 65.7],
          showBackground: true,
          backgroundStyle: {
            color: 'rgba(180, 180, 180, 0.2)'
          }
        }
      ]
    });
  })
  
  return <div id="main" style={{width:'350px',height:'350px'}}></div>;
}

export default MyChart;